@import "../helpers/variables";
@import "../helpers/animation";
@import "../helpers/mixins";

.projects {
  padding-top: $sectionPadding;
  .project-holder {
    display: flex;
    flex-direction: row-reverse;
    background-color: $boxBackground;
    border-radius: 8px;
    gap: 20px;
    overflow: hidden;
    padding: 20px;
    margin-bottom: 25px;
    align-items: center;
    transition-duration: 0.3s;
    @media (max-width: 991px) {
      flex-direction: column-reverse;
    }
    // &:hover {
    //   box-shadow: 0px 0px 9px -2px rgba($color: $secondaryColor, $alpha: 0.9) inset;
    // }
    &:hover .project-info .project-desc {
      color: #ddddddd1;
    }
    .video-holder {
      width: 50%;
      position: relative;
      border-radius: 8px;
      @media (max-width: 991px) {
        width: 100%;
      }
      &::after {
        content: "Hover To Play";
        text-wrap: nowrap;
        position: absolute;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: rgba($color: black, $alpha: 0.8);
        left: 0;
        top: 0;
        color: $primaryColor;
        font-size: 25px;
        border-radius: 8px;
        transition-duration: 0.4s;
        animation: blinking 1s ease-in infinite alternate;
      }
      &:hover::after {
        width: 0;
        opacity: 0;
        font-size: 0;
      }
      .project-video {
        border-radius: 8px;
        transition-duration: 0.3s;
        max-height: 383.35px;
        position: relative;
        width: 100%;
        height: 100%;
        margin-bottom: -6px;
        &:hover {
          box-shadow: 0px 0px 4px 2px #40a0a5;
          transform: scale(1.01);
        }
      }
    }
    .project-info {
      padding: 20px;
      width: 50%;
      @media (max-width: 991px) {
        width: 100%;
        padding: 20px 0;
      }
      .project-name {
        color: $secondaryColor;
        font-size: 40px;
        margin-bottom: 20px;
        @media (max-width: 1199px) {
          font-size: 31px;
        }
        @media (max-width: 525px) {
          font-size: 21px;
        }
      }
      .project-desc {
        color: $paragrahColor;
        line-height: 2;
        font-size: 18px;
        margin-bottom: 30px;
        transition-duration: 0.3s;
        @media (max-width: 1199px) {
          font-size: 16px;
        }
        @media (max-width: 525px) {
          font-size: 14px;
        }
        b {
          color: $secondaryColor;
          transition-duration: 0.2s;
          cursor: default;
          &:hover {
            color: $altSecondaryColor;
          }
        }
      }
    }
  }
}
