@import "../helpers/variables";
@import "../helpers/animation";
@import "../helpers/mixins";

.contact {
  padding-top: $sectionPadding;
  padding-bottom: 80px;
  .container {
    display: flex;
    gap: 20px;
    justify-content: space-around;
    @media (max-width: 767px) {
      flex-direction: column;
      gap: 40px;
    }
    .info {
      .contact-header {
        color: $secondaryColor;
        font-size: 60px;
        margin: 20px 0;
        font-family: "Edu NSW ACT Foundation", cursive;
        font-weight: bold;
      }
      .contact-paragraph {
        color: #ddddddc7;
        font-size: 20px;
        line-height: 1.9;
        max-width: 550px;
        // font-family: "open-sans",sans-serif;
        @media (max-width: 991px) {
          font-size: 18px;
        }
        @media (max-width: 767px) {
          width: 100%;
        }
        @media (max-width: 525px) {
          font-size: 14px;
        }
        svg {
          color: rgba($color: $secondaryColor, $alpha: 0.3);
          margin-left: 5px;
          font-size: 25px;
        }
      }
    }
    form {
      width: 40%;
      @media (max-width: 991px) {
        width: 55%;
      }
      @media (max-width: 767px) {
        width: 100%;
      }
      div {
        position: relative;
        &::before {
          content: "Must be valid";
          position: absolute;
          top: -22px;
          left: 0;
          color: #a82626;
          font-size: 14px;
          opacity: 0;
          transition-duration: 0.3s;
        }
      }
      input:not(input[type="submit"]),
      textarea {
        display: block;
        width: 100%;
        padding: $mainPadding + 5;
        border: 1px solid rgba($color: $secondaryColor, $alpha: 0.4);
        margin-bottom: 30px;
        border-radius: 3px;
        background-color: $boxBackground;
        color: #ddd;
        caret-color: $secondaryColor;
        transition-duration: 0.3s;
        &:focus {
          box-shadow: 0px 1px 6px -2px rgba(
              $color: $secondaryColor,
              $alpha: 0.9
            ) inset;
        }
      }
      textarea {
        height: 250px;
        resize: none;
        line-height: 1.5;
      }
      input[type="submit"] {
        width: fit-content;
        border: none;
        margin: auto;
        padding: $mainPadding;
      }
    }
  }
}

.not-valid {
  &::before {
    opacity: 1 !important;
  }
  input,
  textarea {
    border: 1px solid #b56969 !important;
    background-color: rgb(166 79 79 / 30%) !important;
    box-shadow: none !important;
    color: rgb(224 137 137 / 67%) !important;
  }
}
